import React from 'react';
import './App.css';
import dog_image from './sprites/dog-still.png';
import { Link, useHistory } from 'react-router-dom';
import db from './db';
/* eslint-disable import/no-webpack-loader-syntax */
import Worker from 'worker-loader!./worker.js';

function ButtonLink(props) {
  return props.disabled
    ?
    <div className="App-button App-button-disabled">
      {props.children}
    </div>
    :
    <Link to={props.to} style={{ textDecoration: 'none' }}>
      <div className="App-button App-button-enabled">
        {props.children}
      </div>
    </Link>;
}

function NewGameButton(props) {
  let history = useHistory();
  let onClick = () => {
    function mapRange(value, sourceLow, sourceHigh, targetLow, targetHigh) {
      return Math.min(targetHigh, Math.max(targetLow, Math.round(value / (sourceHigh - sourceLow) * (targetHigh - targetLow)) + targetLow));
    }
    let difficulty = parseInt(props.difficulty, 10);
    let mappedDifficulty = mapRange(difficulty, 0, 100, window.localStorage.getItem("testMode") ? 2 : 40, 75);
    let worker = new Worker();

    worker.addEventListener('message', (e) => {
      db.table('unsolved')
        .add({ date: new Date(), grid: e.data })
        .then(id => history.push(`/dachshun-doku/game/${id}`));
    });

    worker.postMessage(mappedDifficulty);
  }
  return (
    <div className="App-button App-button-enabled" onClick={onClick}>
      new game
    </div>
  );
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { difficulty: window.localStorage.getItem('difficulty') || 50 };
  }

  onDifficultyChanged(event) {
    let difficulty = event.target.value;
    this.setState({ difficulty: difficulty });
    window.localStorage.setItem('difficulty', difficulty);
  }

  render() {
    let currentId = window.localStorage.getItem('currentId');
    return (
      <div className="App">
        <header className="App-header">
          <img className="App-title-image" src={dog_image} alt="" />
          <h1>
            DACHSHUN-DOKU
          </h1>
          <section className="App-buttons">
            <ButtonLink to={`/dachshun-doku/game/${currentId}`} disabled={!currentId}>continue</ButtonLink>
            <NewGameButton difficulty={this.state.difficulty} />

            <div>
              <input type="range" id="difficulty-slider" step="10" defaultValue={this.state.difficulty} onChange={this.onDifficultyChanged.bind(this)} />
              difficulty
            </div>

            <ButtonLink to="/dachshun-doku/unsolved">unsolved</ButtonLink>
          </section>
        </header>
      </div>
    );
  }
}

export default App;
